
nav {
    height: 4rem;
    width: 100vw;
    background: var(--color-primary-variant);
    display: flex;
    place-items: center;
    position: fixed;
    top: 0;
    z-index: 99;
}

.nav_toggle-btn {
    display: none;
}

.nav_container{
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
.badge h1 {
    letter-spacing: 0.2rem;
    color: var(--color-bg);
    font-weight: 700;
    width: fit-content;
    align-self: center;
    margin-top: 1.5rem;
    /* border: 1px solid; */
}

.logo {
    width: 5rem;
    display: block;
}

.nav_links {
    max-width: 60%;
    display: flex;
    gap: 1.2rem;
    align-items: center;
}
.active-nav {
    position: relative;
    /* border: 1px solid red; */
}

.nav_links a {
    transition: var(--transition);
}

.nav_links a:hover {
    color: var(--color-white);
}

.active-nav::after {
    content: '';
    display: block;
    width: 1.2rem;
    height: 1.2rem;
    background: var(--color-light);
    position: absolute;
    left: calc(50% - 0.8rem);
    transform: rotate(45deg);
    margin-top: 1rem;
    transition: var(--transition);
}


@media screen and (max-width: 1024px){
    .nav_toggle-btn {
        display: inline-block;
        background: transparent;
        font-size: 1rem;
        cursor: pointer;
    }  
    .nav_toggle-btn svg {
        color: var(--color-bg);
    }
    .nav_links {
        position: absolute;
        top: 100%;
        right: 0;
        flex-direction: column;
        gap: 0;
        perspective: 500px;
    }
    .active-nav, .active-nav::after {
        display: none;
    }

    .nav_links li {
        height: 4rem;
        width: 100%;
        box-shadow: -2rem 2rem 5rem rgba(0,0,0,0.3);
        animation: navAnimation 600ms ease forwards;
        transform: rotateX(90deg);
        opacity: 0;
        transform-origin: top;
    }
    .nav_links li:nth-child(2) {
        animation-delay: 200ms;
    }
    .nav_links li:nth-child(3) {
        animation-delay: 400ms;
    }
    .nav_links li:nth-child(4) {
        animation-delay: 600ms;
    }
    .nav_links li:nth-child(5) {
        animation-delay: 800ms;
    }
    .nav_links li:nth-child(6) {
        animation-delay: 1s;
    }
    @keyframes navAnimation {
        to {
            transform: rotateX(0);
            opacity: 1;
        }
    }

    .nav_links li a {
        background-color: var(--color-primary-variant);
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        padding: 1rem 3rem;
    }
    .show_nav {
        display: flex;
    }
    .hide_nav {
        display: none;
    }
}

